import React, { useState,useEffect } from "react";
import Header from "./component/Header";
import Footer from "./component/Footer";
import CreateNote from "./component/CreateNote";
import Note from "./component/Note";
import "./index.css";
// import { useEffect } from "react";

const App = () => {
  const [arr1, setArr] = useState([]);

  const addNote = (note) => {
    setArr((olditems) => {
      localStorage.setItem("note", JSON.stringify([...olditems, note])); 
      return [...olditems, note];
    });
  };

  const delTheNote = (id) => {
    setArr((olditems) => {
      const resultingArr = olditems.filter((arrElem, index) => {
        return index !== id;
      });
      localStorage.setItem("note", JSON.stringify(resultingArr)); 
      return resultingArr;
    });
  };


  useEffect(() => {
    alert("Hi");
    let data = JSON.parse(localStorage.getItem("note"));
    if (data===[]){
      localStorage.setItem("note",[]);
    }
    setArr(data);
  }, []);

  return (
    <>
      <Header />
      <CreateNote passNote={addNote} />

      {arr1.map((val, index) => {
        return (
          <Note
            id={index}
            key={index}
            title={val.title}
            content={val.para}
            deleteNote={delTheNote}
          />
        );
      })}

      <Footer />
    </>
  );
};

export default App;

// Google Keep clone ends here
