import React ,{ useState } from "react";
import { Button } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';


const CreateNote = (props)=>{
  
    const [note,setNote] = useState({
        title:"",
        para:""
    });
  
    const [expand,setEx] = useState(false);

    const change = (event)=>{

        const {name,value} = event.target;
        
        setNote((prevValue)=>{
            return ({
                ...prevValue,
                [name]:value
            });
        });

    }
  
    const putInArr = ()=>{

        props.passNote(note);

        setNote({
            title:"",
            para:""
        });
    }

    return(
        <>
        <div className="main_note" 
            onClick={()=>{setEx(true)}}
            onDoubleClick={()=>{setEx(false)}}
            >
            <form action="">
                

                {expand?<input 
                    name="title" 
                    type="text"
                    placeholder="Title"
                    onChange={change}
                    value={note.title}
                    autoComplete="off" 
                />:null}
        
                <textarea
                    name="para"
                    id="" 
                    cols=""
                    rows=""
                    placeholder="Write a note..."
                    onChange={change} 
                    value={note.para}  
                />
                {   expand?
                    <Button>
                        <AddIcon onClick={putInArr}/>
                    </Button>:null
                }    
            </form>
        </div>
            
        </>
    );
};

export default CreateNote;
